.App {
	text-align: center;
}

.ciclista {
	display: grid;
	grid-template-columns: 1fr 4fr 5fr 5fr 1fr 1fr;
	padding: 3px;
	background-color: #f4f4f4;
}

.ciclista:nth-child(even) {
	background-color: #f4ffff;
}

.ciclista img {
	width: 60px;
	height: 60px;
	object-fit: cover;
	border-radius: 50%;
	box-shadow: var(--light-shadow);
}

.ciclista a {
	display: flex;
	align-items: center;
	justify-content: center;
}

.nombre {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	text-align: start;
}

button {
	background-color: #282c34;
	color: white;
	border: none;
	height: 30px;
	align-self: center;
	transition: all 0.3s ease-in-out;
}

button:hover {
	background-color: red;
}
